import React, { Component } from "react";
import ActionItem from './ActionItem';
import Skill from './Skill';
import Subtask from './Subtask';
import Stage from '../Stage/Stage'
import "./SprintPlanning.css";
import {Card, Col} from 'react-materialize'


const actionItemNum = 7;
class SprintPlanning extends Component {
    state = {
        skill: "Archery",
        subtask: "wind calibration",
        actionItems: []
    };

    componentDidMount() {
        this.loadSprintPlan();
    }
    loadSprintPlan = () => {
        const ai = []
        for (let i = 0; i < actionItemNum; i++) {
            ai.push("");
        }
        this.setState({actionItems: ai});
    }

    handleInputChange = event => {
        const { name, value } = event.target;
        let newArr = this.state.actionItems.slice();
        newArr[name] = value;
        this.setState({
            actionItems: newArr
        });
      };

    renderActionItems = () => {
        const ai = []
        for (let i = 0; i < actionItemNum; i++) {
            ai.push(<ActionItem 
            actionItem={this.state.actionItems[i]} 
            id={i} 
            handleInputChange = {this.handleInputChange}
            />)
        }
        return ai;
    }
    render() {
        return (
            <Stage>
                <Skill />
                <Subtask />
                <form>
                    {this.renderActionItems()}
                </form>
                <a class="btn-floating btn-large waves-effect waves-light red"><i class="material-icons">add</i></a>
            </Stage>
        );
    }
}

export default SprintPlanning;



