import React, { Component } from "react";
import API from "../../utils/API";
import TodayContainer from "./TodayContainer";
import ProgressContainer from "./ProgressContainer";
import Stage from '../Stage/Stage'
import CheckInContainer from "./CheckInContainer";
import {Card, Col} from 'react-materialize'

import "./Scrum.css";

class Scrum extends Component {
    state = {
        calItems: [{
            event: "haircut",
            time: "11:00",
            duration: 1
        }, {
            event: "meeting",
            time: "13:00",
            duration: 1.5
        }, {
            event: "dinner gala",
            time: "18:30",
            duration: 2.5
        }],
        sprintNum: 0,
        skill: "Archery",
        date: new Date().toLocaleString('en-US',
            {
                weekday: 'long',
                year: 'numeric',
                month: 'long',
                day: 'numeric'
            }
        ),
        prevTodos: ["lick a cow", "eat a peach"],
        todos: ["wear pants", "hula-hoop"],
        actionItem: "poke people",
        sprintDays: 3,
        skillDays: 10,
        yearDays: 152
    };


    componentDidMount() {
        this.loadData();
    }

    loadData = () => {
        // API.getSkill().then();
        // API.getCalItems().then();
        // API.getSprintNum().then();
        // API.getDate().then();
        // API.prevTodo().then();
        // API.getTodos().then();
        // API.getSprintDays().then();
        // API.getSkillDays().then();
        // API.getYearDays().then();
    }

    render() {
        return (
            <Stage>
                <TodayContainer
                    calItems={this.state.calItems}
                    skill={this.state.skill}
                    date={this.state.date}
                    actionItem={this.state.actionItem}
                    sprintNum={this.state.sprintNum}
                />
                <ProgressContainer
                    skillDays={this.state.skillDays}
                    sprintDays={this.state.sprintDays}
                    yearDays={this.state.yearDays}
                />
                <CheckInContainer
                    todos={this.state.todos}
                    prevTodos={this.state.prevTodos}
                />
                <a class="btn-floating btn-large waves-effect waves-light red"><i class="material-icons">add</i></a>
            </Stage>
        );
    }
}

export default Scrum;



